:host {
  display: block;
}

.ui-paragraph {
  margin: 0;
}

.link-wrapper {
  display: flex;
}

.image {
  display: inline-block;
  margin-inline-end: 7px;
  width: 28px;
  height: 28px;
  border-radius: 100%;
}

.info {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
}
.info .name {
  color: #010610;
}
.info .title {
  color: #92A1B3;
}